@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i);
html, body {
    overflow-x: hidden;
    max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



/*Font CSS*/

@font-face {
    font-family: GilroyExtraBold;
    src: url(/guillermoroman/static/media/Gilroy-ExtraBold.0f6e082f.otf) format("opentype");
}

@font-face {
    font-family: GilroyLight;
    src: url(/guillermoroman/static/media/Gilroy-Light.c62aded7.otf) format("opentype");
}

/*Header CSS*/

.initialRedBox{
	width:50%;
    height: 120vh;
	background-color: #ff725c;
	display: inline-block;
	transition: width 0.9s ease 0ms;
	z-index: -5;
}

.expandRedBox {
	width: 100%;
}

.welcomeMessage {
    font-family: GilroyExtraBold;
    font-size: 3vw;
    position: absolute;
    top: 60%;
    left: 25%;
    width: 30%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.helloInitial {
    font-family: GilroyExtraBold;
    font-size: 7vw;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: all 0.9s ease 0ms;
}

.helloFinal {
    left: 100%;
}

.initialGrayBox {
    font-family: GilroyExtraBold;
	width:60%;
	background-color: #1c1b20;
    position: absolute;
    top: 30%;
    right: -100%;
    transition: all 1.5s ease 0ms;
    color: #f4f4f4;
}

.finalGrayBox {
    right: 0%;
}

.skillGrid {
    margin:0 auto;
    width: 80%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: stretch;
/*    grid-template-rows: 1fr 1fr;*/
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

h3 {
    font-size: 2rem;
}

h3 + p {
    font-size: 1.5rem;
    color: #4c4a57;
}

/*About CSS */ 

.boxAbout {
    padding: 20px 0px 20px 0px;
    background-color: #f4f4f4;
}

.aboutContent {
    padding: 2rem 1rem 0 3rem;
}

.fixed {
	position: fixed;
}

#parallaxAbout {
    font-size: 3vw;
    font-family: GilroyExtraBold;
    background-image: url("https://images.unsplash.com/photo-1532289402244-3cbf8bdeb722?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1650&q=80");
    padding-Top: 50vh;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.aboutParagraph {
    width: 49%;
    display: inline-block;
    font-size: 2rem;
}

/*Portfolio CSS*/

#Portfolio {
    margin-top: -20px;
    padding: 13rem 30px 20px 3rem;
    margin-top: 0;
    display: block;
    height: 100%; 
    background-color:#f4f4f4;
}

.title {
    color: #1c1b20;
    line-height: 0.9;
    font-size: 6rem;
    font-weight: bold;
    font-family: GilroyExtraBold;
}

.pushRight {
    margin-left: 20%;
}

.pushRight2 {
    margin-left: 55%;
}

.pushLeft {
    padding-right: 20%;
}

.portfolioParagraph {
    font-family: 'Libre Baskerville', serif;
    font-size: 2.2rem;
    line-height: 0.9;
}

.portfolioParagraph2 {
    font-family: GilroyExtraBold;
    font-style: normal;
    font-size: 1.8rem;
}

.portfolioParagraph3 {
    font-family: GilroyLight;
    font-size: 1rem;
    width: 30%;
}

.portfolioImage {
    float: left;
    height: 300px;
    width: 50%;
    display: inline;
}

/*Media CSS*/

@media (max-width: 1100px) {
    h3 {
        font-size: 1.4rem;
    }

    h3 + p {
        font-size: 1.1rem;
    }
    #parallaxAbout {
        padding-top: 35vh;
    }
    .expandRedBox {
        height:95vh;
    }
    .aboutParagraph {
        font-size: 1.9rem;
    }

    .boxAbout {
        font-size: 2rem;
        height: 80vh; 
    }
    #Portfolio {
    padding: 7rem 30px 20px 3rem;
    }
}

@media (max-width: 900px) {
    .aboutParagraph {
        font-size: 1.3rem;
    }
    .title {
        font-size: 5rem;
    }
}

@media (max-width: 700px) {
    h3 {
        font-size: 1.2rem;
    }

    h3 + p {
        font-size: 1rem;
    }
    .boxAbout {
        font-size: 1.9rem;
        height: 80vh; 
    }
    .aboutParagraph {
        width: 100%;
        font-size: 1.5rem;
        margin-top: -20px;
    }
    .title {
        font-size: 4rem;
    }
}

@media (max-width: 600px) {
    h3 {
        font-size: 1.1rem;
    }

    h3 + p {
        font-size: 0.9rem;
    }
    .welcomeMessage {
        font-size: 1rem;
    }

    .expandRedBox {
        height:100vh;
    }

    #parallaxAbout {
        font-size: 5vw;
        padding-top: 25vh;
        background-image: url("https://images.pexels.com/photos/953214/pexels-photo-953214.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    }

    .aboutParagraph {
        width: 100%;
        margin-top: -10px;
        font-size: 1.3rem;
        display: block;
    }
    .boxAbout {
        font-size: 3.8vh;
        height: 80vh; 
    }

    .title {
        font-size: 3rem;
    }

    .pushRight {
    padding-left: 5%;
    }

    .pushRight2 {
        padding-left: 5%;
    }
    #Portfolio {
    padding: 3rem 15px 20px 15px;
    }
    .aboutContent {
    padding: 2rem 15px 0 15px;
    }
}
